import {
  Edit,
  Trash2,
  Eye,
  Calendar,
  Clock,
  Star,
  Globe,
  Linkedin,
  Mail,
  Award,
  GraduationCap,
  AlertCircle,
} from "lucide-react";
import { useCompaniesContext } from "../../context/CompaniesContext";

export default function CompanyCardView({
  company,
  statusColors,
  companyTypeColors,
}) {
  const { formatPackage, setSelectedCompany } = useCompaniesContext();
  return (
    <div className="bg-white rounded-xl shadow-sm border border-gray-100 hover:shadow-md transition-all duration-200 overflow-hidden">
      {/* Header */}
      <div className="p-6 border-b border-gray-50">
        <div className="flex items-start justify-between">
          <div className="flex-1">
            <div className="flex items-center gap-3 mb-2">
              <h3 className="text-lg font-semibold text-gray-900">
                {company.company_name}
              </h3>
              {company.is_marquee && (
                <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-gradient-to-r from-yellow-100 to-orange-100 text-yellow-800 border border-yellow-200">
                  <Award size={12} className="mr-1" />
                  Marquee
                </span>
              )}
            </div>
            <p className="text-sm text-gray-600 mb-3 line-clamp-2">
              {company.company_description}
            </p>

            <div className="flex items-center gap-4 text-xs text-gray-500">
              <span
                className={`inline-flex px-2 py-1 rounded-full font-medium border ${statusColors[company.status]}`}
              >
                {company.status.charAt(0).toUpperCase() +
                  company.status.slice(1)}
              </span>
              <span
                className={`inline-flex px-2 py-1 rounded-full font-medium ${companyTypeColors[company.company_type]}`}
              >
                {company.company_type.charAt(0).toUpperCase() +
                  company.company_type.slice(1)}
              </span>
            </div>
          </div>

          <div className="flex items-center gap-2">
            {company.glassdoor_rating && (
              <div className="flex items-center gap-1 bg-green-50 px-2 py-1 rounded-lg">
                <Star size={14} className="text-yellow-500 fill-current" />
                <span className="text-sm font-medium text-green-700">
                  {company.glassdoor_rating}
                </span>
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Content */}
      <div className="p-6">
        <div className="grid grid-cols-2 gap-4 mb-4">
          <div className="space-y-2">
            <div className="flex items-center gap-2 text-sm">
              <Calendar size={14} className="text-gray-400" />
              <span className="text-gray-600">Visit:</span>
              <span className="font-medium">
                {new Date(company.scheduled_visit).toLocaleDateString()}
              </span>
            </div>
            {company.actual_arrival && (
              <div className="flex items-center gap-2 text-sm">
                <Clock size={14} className="text-gray-400" />
                <span className="text-gray-600">Arrived:</span>
                <span className="font-medium">
                  {new Date(company.actual_arrival).toLocaleDateString()}
                </span>
              </div>
            )}
          </div>

          <div className="space-y-2">
            <div className="flex items-center gap-2 text-sm">
              <GraduationCap size={14} className="text-gray-400" />
              <span className="text-gray-600">Min CGPA:</span>
              <span className="font-medium">{company.min_cgpa}</span>
            </div>
            <div className="flex items-center gap-2 text-sm">
              <AlertCircle size={14} className="text-gray-400" />
              <span className="text-gray-600">Max Backlogs:</span>
              <span className="font-medium">{company.max_backlogs}</span>
            </div>
          </div>
        </div>

        {/* Positions */}
        <div className="mb-4">
          <h4 className="text-sm font-medium text-gray-700 mb-2">
            Open Positions
          </h4>
          <div className="flex flex-wrap gap-2">
            {company.positions.map((position) => (
              <div
                key={position.id}
                className="bg-blue-50 text-blue-700 px-3 py-1 rounded-lg text-sm"
              >
                <span className="font-medium">{position.position_title}</span>
                <span className="text-blue-600 ml-2">
                  {formatPackage(position.package_range)}
                </span>
              </div>
            ))}
          </div>
        </div>

        {/* Stats */}
        <div className="grid grid-cols-3 gap-4 mb-4">
          <div className="text-center p-2 bg-gray-50 rounded-lg">
            <div className="text-lg font-semibold text-gray-900">
              {company.applied}
            </div>
            <div className="text-xs text-gray-600">Applied</div>
          </div>
          <div className="text-center p-2 bg-green-50 rounded-lg">
            <div className="text-lg font-semibold text-green-600">
              {company.selected}
            </div>
            <div className="text-xs text-gray-600">Selected</div>
          </div>
          <div className="text-center p-2 bg-blue-50 rounded-lg">
            <div className="text-lg font-semibold text-blue-600">
              {company.positions.length}
            </div>
            <div className="text-xs text-gray-600">Positions</div>
          </div>
        </div>

        {/* Actions */}
        <div className="flex items-center justify-between pt-4 border-t border-gray-50">
          <div className="flex items-center gap-2">
            {company.website_url && (
              <button className="text-gray-400 hover:text-blue-600 transition-colors">
                <Globe size={16} />
              </button>
            )}
            {company.linkedin_url && (
              <button className="text-gray-400 hover:text-blue-600 transition-colors">
                <Linkedin size={16} />
              </button>
            )}
            <button className="text-gray-400 hover:text-green-600 transition-colors">
              <Mail size={16} />
            </button>
          </div>

          <div className="flex items-center gap-2">
            <button
              onClick={() => setSelectedCompany(company)}
              className="text-blue-600 hover:text-blue-800 transition-colors"
            >
              <Eye size={16} />
            </button>
            <button className="text-gray-600 hover:text-gray-800 transition-colors">
              <Edit size={16} />
            </button>
            <button className="text-red-600 hover:text-red-800 transition-colors">
              <Trash2 size={16} />
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
