import {
  Calendar,
  Users,
  Clock,
  X,
  Globe,
  Linkedin,
  Mail,
  Phone,
  Award,
  MapPin,
  Briefcase,
  GraduationCap,
  AlertCircle,
  ExternalLink,
  TrendingUp,
} from "lucide-react";
import { useCompaniesContext } from "../../context/CompaniesContext";

export function CompanyDetailModal() {
  const { selectedCompany, setSelectedCompany, formatPackage } =
    useCompaniesContext();

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <div className="bg-white rounded-xl max-w-4xl w-full max-h-[90vh] overflow-y-auto">
        {/* Modal Header */}
        <div className="sticky top-0 bg-white border-b border-gray-200 px-6 py-4 flex items-center justify-between">
          <div>
            <h2 className="text-2xl font-bold text-gray-900 flex items-center gap-3">
              {selectedCompany.company_name}
              {selectedCompany.is_marquee && (
                <span className="inline-flex items-center px-3 py-1 rounded-full text-sm font-medium bg-gradient-to-r from-yellow-100 to-orange-100 text-yellow-800 border border-yellow-200">
                  <Award size={16} className="mr-1" />
                  Marquee Company
                </span>
              )}
            </h2>
            <p className="text-gray-600 mt-1">
              {selectedCompany.company_description}
            </p>
          </div>
          <button
            onClick={() => setSelectedCompany(null)}
            className="text-gray-400 hover:text-gray-600 transition-colors"
          >
            <X size={24} />
          </button>
        </div>

        <div className="p-6">
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
            {/* Left Column - Company Info */}
            <div className="lg:col-span-2 space-y-6">
              {/* Contact Information */}
              <div className="bg-gray-50 rounded-xl p-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center gap-2">
                  <Users size={20} />
                  Primary Contact
                </h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <label className="text-sm font-medium text-gray-600">
                      HR Manager
                    </label>
                    <p className="text-gray-900 font-medium">
                      {selectedCompany.primary_hr_name}
                    </p>
                    <p className="text-sm text-gray-600">
                      {selectedCompany.primary_hr_designation}
                    </p>
                  </div>
                  <div>
                    <label className="text-sm font-medium text-gray-600">
                      Contact Details
                    </label>
                    <div className="space-y-1">
                      <div className="flex items-center gap-2 text-sm">
                        <Mail size={14} />
                        <a
                          href={`mailto:${selectedCompany.primary_hr_email}`}
                          className="text-blue-600 hover:underline"
                        >
                          {selectedCompany.primary_hr_email}
                        </a>
                      </div>
                      <div className="flex items-center gap-2 text-sm">
                        <Phone size={14} />
                        <span>{selectedCompany.primary_hr_phone}</span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              {/* Positions */}
              <div className="bg-white border border-gray-200 rounded-xl p-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center gap-2">
                  <Briefcase size={20} />
                  Open Positions ({selectedCompany.positions.length})
                </h3>
                <div className="space-y-4">
                  {selectedCompany.positions.map((position) => (
                    <div
                      key={position.id}
                      className="border border-gray-100 rounded-lg p-4"
                    >
                      <div className="flex items-start justify-between mb-2">
                        <h4 className="font-semibold text-gray-900">
                          {position.position_title}
                        </h4>
                        <div className="text-right">
                          <div className="text-lg font-bold text-green-600">
                            {formatPackage(position.package_range)}
                          </div>
                          <div className="text-xs text-gray-500 capitalize">
                            {position.job_type.replace("_", " ")}
                          </div>
                        </div>
                      </div>
                      <div className="flex items-center gap-4 text-sm text-gray-600">
                        <span
                          className={`px-2 py-1 rounded-full text-xs ${position.is_active ? "bg-green-100 text-green-800" : "bg-gray-100 text-gray-800"}`}
                        >
                          {position.is_active ? "Active" : "Inactive"}
                        </span>
                      </div>
                    </div>
                  ))}
                </div>
              </div>

              {/* Requirements */}
              <div className="bg-blue-50 rounded-xl p-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center gap-2">
                  <GraduationCap size={20} />
                  Eligibility Requirements
                </h3>
                <div className="grid grid-cols-2 gap-4">
                  <div className="bg-white rounded-lg p-4">
                    <div className="text-2xl font-bold text-blue-600">
                      {selectedCompany.min_cgpa}
                    </div>
                    <div className="text-sm text-gray-600">Minimum CGPA</div>
                  </div>
                  <div className="bg-white rounded-lg p-4">
                    <div className="text-2xl font-bold text-orange-600">
                      {selectedCompany.max_backlogs}
                    </div>
                    <div className="text-sm text-gray-600">
                      Maximum Backlogs
                    </div>
                  </div>
                </div>
                {selectedCompany.bond_required && (
                  <div className="mt-4 p-3 bg-orange-100 border border-orange-200 rounded-lg">
                    <div className="flex items-center gap-2 text-orange-800">
                      <AlertCircle size={16} />
                      <span className="font-medium">Bond Required</span>
                    </div>
                    <p className="text-sm text-orange-700 mt-1">
                      This company requires a service bond agreement.
                    </p>
                  </div>
                )}
              </div>
            </div>

            {/* Right Column - Stats & Schedule */}
            <div className="space-y-6">
              {/* Quick Stats */}
              <div className="bg-white border border-gray-200 rounded-xl p-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center gap-2">
                  <TrendingUp size={20} />
                  Statistics
                </h3>
                <div className="space-y-4">
                  <div className="flex items-center justify-between">
                    <span className="text-gray-600">Applications</span>
                    <span className="text-2xl font-bold text-blue-600">
                      {selectedCompany.applied}
                    </span>
                  </div>
                  <div className="flex items-center justify-between">
                    <span className="text-gray-600">Selected</span>
                    <span className="text-2xl font-bold text-green-600">
                      {selectedCompany.selected}
                    </span>
                  </div>
                  <div className="flex items-center justify-between">
                    <span className="text-gray-600">Selection Rate</span>
                    <span className="text-lg font-semibold text-purple-600">
                      {selectedCompany.applied > 0
                        ? `${((selectedCompany.selected / selectedCompany.applied) * 100).toFixed(1)}%`
                        : "0%"}
                    </span>
                  </div>
                  {selectedCompany.applied > 0 && (
                    <div className="w-full bg-gray-200 rounded-full h-3">
                      <div
                        className="bg-gradient-to-r from-green-500 to-green-600 h-3 rounded-full transition-all"
                        style={{
                          width: `${(selectedCompany.selected / selectedCompany.applied) * 100}%`,
                        }}
                      ></div>
                    </div>
                  )}
                </div>
              </div>

              {/* Schedule */}
              <div className="bg-green-50 rounded-xl p-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center gap-2">
                  <Calendar size={20} />
                  Schedule
                </h3>
                <div className="space-y-3">
                  <div className="flex items-center gap-3">
                    <Calendar size={16} className="text-blue-500" />
                    <div>
                      <div className="text-sm font-medium text-gray-900">
                        Scheduled Visit
                      </div>
                      <div className="text-lg font-semibold text-blue-600">
                        {new Date(
                          selectedCompany.scheduled_visit
                        ).toLocaleDateString("en-US", {
                          weekday: "long",
                          year: "numeric",
                          month: "long",
                          day: "numeric",
                        })}
                      </div>
                    </div>
                  </div>
                  {selectedCompany.actual_arrival && (
                    <div className="flex items-center gap-3">
                      <Clock size={16} className="text-green-500" />
                      <div>
                        <div className="text-sm font-medium text-gray-900">
                          Actual Arrival
                        </div>
                        <div className="text-lg font-semibold text-green-600">
                          {new Date(
                            selectedCompany.actual_arrival
                          ).toLocaleDateString("en-US", {
                            weekday: "long",
                            year: "numeric",
                            month: "long",
                            day: "numeric",
                          })}
                        </div>
                      </div>
                    </div>
                  )}
                </div>
              </div>

              {/* Quick Links */}
              <div className="bg-white border border-gray-200 rounded-xl p-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center gap-2">
                  <ExternalLink size={20} />
                  Quick Links
                </h3>
                <div className="space-y-3">
                  {selectedCompany.website_url && (
                    <a
                      href={selectedCompany.website_url}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="flex items-center gap-3 p-3 bg-gray-50 rounded-lg hover:bg-gray-100 transition-colors"
                    >
                      <Globe size={16} className="text-blue-600" />
                      <span className="text-sm font-medium">
                        Company Website
                      </span>
                    </a>
                  )}
                  {selectedCompany.linkedin_url && (
                    <a
                      href={selectedCompany.linkedin_url}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="flex items-center gap-3 p-3 bg-gray-50 rounded-lg hover:bg-gray-100 transition-colors"
                    >
                      <Linkedin size={16} className="text-blue-600" />
                      <span className="text-sm font-medium">LinkedIn Page</span>
                    </a>
                  )}
                  <a
                    href={`mailto:${selectedCompany.primary_hr_email}`}
                    className="flex items-center gap-3 p-3 bg-gray-50 rounded-lg hover:bg-gray-100 transition-colors"
                  >
                    <Mail size={16} className="text-green-600" />
                    <span className="text-sm font-medium">Contact HR</span>
                  </a>
                </div>
              </div>

              {/* Work Locations */}
              <div className="bg-purple-50 rounded-xl p-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center gap-2">
                  <MapPin size={20} />
                  Work Locations
                </h3>
                <div className="flex flex-wrap gap-2">
                  {selectedCompany.work_locations
                    .split(", ")
                    .map((location, index) => (
                      <span
                        key={index}
                        className="px-3 py-1 bg-purple-100 text-purple-800 rounded-full text-sm font-medium"
                      >
                        {location.trim()}
                      </span>
                    ))}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
