"use client";

import { useState } from "react";
import {
  Plus,
  Search,
  Calendar,
  Users,
  Clock,
  Building2,
  Award,
} from "lucide-react";
import {
  CompaniesProvider,
  useCompaniesContext,
} from "../../context/CompaniesContext";
import CompanyCardView from "./CompanyCardView";
import { CompanyDetailModal } from "./CompanyDetailModal";
import { CompanyTableView } from "./CompanyTableView";

const CompanyListing = () => {
  const [activeTab, setActiveTab] = useState("all");
  const [searchTerm, setSearchTerm] = useState("");
  const [filterStatus, setFilterStatus] = useState("all");
  const [filterType, setFilterType] = useState("all");
  const [showAddModal, setShowAddModal] = useState(false);
  const [viewMode, setViewMode] = useState("table"); // table or cards
  const { selectedCompany } = useCompaniesContext();

  // Enhanced sample data matching database schema
  const [companies, setCompanies] = useState([
    {
      id: 1,
      company_name: "Google India Pvt Ltd",
      company_description:
        "Leading technology company focused on internet-related services and products",
      company_type: "tech",
      is_marquee: true,
      website_url: "https://careers.google.com/",
      linkedin_url: "https://linkedin.com/company/google",
      primary_hr_name: "Priya Sharma",
      primary_hr_designation: "Senior HR Manager",
      primary_hr_email: "priya.sharma@google.com",
      primary_hr_phone: "+91-9876543210",
      scheduled_visit: "2024-08-20",
      actual_arrival: "2024-08-20",
      glassdoor_rating: 4.5,
      work_locations: "Bangalore, Hyderabad, Gurgaon",
      min_cgpa: 7.5,
      max_backlogs: 2,
      bond_required: false,
      status: "active",
      positions: [
        {
          id: 1,
          position_title: "Software Development Engineer",
          job_type: "full_time",
          package_range: 2500000,
          is_active: true,
        },
        {
          id: 2,
          position_title: "Product Manager",
          job_type: "full_time",
          package_range: 3000000,
          is_active: true,
        },
      ],
      applied: 156,
      selected: 12,
      batches: ["2024", "2025"],
    },
    {
      id: 2,
      company_name: "Microsoft India",
      company_description:
        "Multinational technology corporation developing software, services, and solutions",
      company_type: "tech",
      is_marquee: true,
      website_url: "https://careers.microsoft.com/",
      linkedin_url: "https://linkedin.com/company/microsoft",
      primary_hr_name: "Rahul Gupta",
      primary_hr_designation: "Talent Acquisition Lead",
      primary_hr_email: "rahul.gupta@microsoft.com",
      primary_hr_phone: "+91-9876543211",
      scheduled_visit: "2024-08-25",
      actual_arrival: null,
      glassdoor_rating: 4.2,
      work_locations: "Noida, Pune, Hyderabad",
      min_cgpa: 8.0,
      max_backlogs: 1,
      bond_required: false,
      status: "upcoming",
      positions: [
        {
          id: 3,
          position_title: "Software Engineer",
          job_type: "full_time",
          package_range: 2200000,
          is_active: true,
        },
        {
          id: 4,
          position_title: "Data Scientist",
          job_type: "full_time",
          package_range: 2800000,
          is_active: true,
        },
      ],
      applied: 0,
      selected: 0,
      batches: ["2024", "2025"],
    },
    {
      id: 3,
      company_name: "Amazon Development Centre",
      company_description:
        "Global e-commerce and cloud computing company offering diverse technology roles",
      company_type: "tech",
      is_marquee: true,
      website_url: "https://amazon.jobs/",
      linkedin_url: "https://linkedin.com/company/amazon",
      primary_hr_name: "Anjali Verma",
      primary_hr_designation: "Campus Recruitment Manager",
      primary_hr_email: "anjali.verma@amazon.com",
      primary_hr_phone: "+91-9876543212",
      scheduled_visit: "2024-07-30",
      actual_arrival: "2024-08-02",
      glassdoor_rating: 4.0,
      work_locations: "Chennai, Bangalore, Mumbai",
      min_cgpa: 7.0,
      max_backlogs: 3,
      bond_required: false,
      status: "completed",
      positions: [
        {
          id: 5,
          position_title: "SDE-1",
          job_type: "full_time",
          package_range: 2000000,
          is_active: true,
        },
        {
          id: 6,
          position_title: "Support Engineer",
          job_type: "full_time",
          package_range: 1800000,
          is_active: true,
        },
      ],
      applied: 220,
      selected: 18,
      batches: ["2024"],
    },
    {
      id: 4,
      company_name: "Infosys Limited",
      company_description:
        "Global leader in next-generation digital services and consulting",
      company_type: "service",
      is_marquee: false,
      website_url: "https://infosys.com/careers",
      linkedin_url: "https://linkedin.com/company/infosys",
      primary_hr_name: "Deepak Kumar",
      primary_hr_designation: "Recruitment Specialist",
      primary_hr_email: "deepak.kumar@infosys.com",
      primary_hr_phone: "+91-9876543213",
      scheduled_visit: "2024-09-10",
      actual_arrival: null,
      glassdoor_rating: 3.8,
      work_locations: "Multiple Locations",
      min_cgpa: 6.5,
      max_backlogs: 5,
      bond_required: true,
      status: "upcoming",
      positions: [
        {
          id: 7,
          position_title: "Systems Engineer",
          job_type: "full_time",
          package_range: 1200000,
          is_active: true,
        },
        {
          id: 8,
          position_title: "Specialist Programmer",
          job_type: "full_time",
          package_range: 1800000,
          is_active: true,
        },
      ],
      applied: 0,
      selected: 0,
      batches: ["2024", "2025"],
    },
  ]);

  const statusColors = {
    active: "bg-green-100 text-green-800 border-green-200",
    upcoming: "bg-blue-100 text-blue-800 border-blue-200",
    completed: "bg-gray-100 text-gray-800 border-gray-200",
  };

  const companyTypeColors = {
    tech: "bg-purple-100 text-purple-800",
    service: "bg-indigo-100 text-indigo-800",
    consulting: "bg-cyan-100 text-cyan-800",
    finance: "bg-emerald-100 text-emerald-800",
    healthcare: "bg-rose-100 text-rose-800",
  };

  const filteredCompanies = companies.filter((company) => {
    const matchesSearch =
      company.company_name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      company.company_description
        .toLowerCase()
        .includes(searchTerm.toLowerCase());

    const matchesStatus =
      filterStatus === "all" || company.status === filterStatus;
    const matchesType =
      filterType === "all" || company.company_type === filterType;

    const matchesTab =
      activeTab === "all" ||
      (activeTab === "marquee" && company.is_marquee) ||
      (activeTab === "active" && company.status === "active") ||
      (activeTab === "upcoming" && company.status === "upcoming");

    return matchesSearch && matchesStatus && matchesType && matchesTab;
  });

  const getStats = () => {
    const total = companies.length;
    const marquee = companies.filter((c) => c.is_marquee).length;
    const active = companies.filter((c) => c.status === "active").length;
    const upcoming = companies.filter((c) => c.status === "upcoming").length;
    const completed = companies.filter((c) => c.status === "completed").length;

    return { total, marquee, active, upcoming, completed };
  };

  const stats = getStats();

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-100">
      <div className="max-w-7xl mx-auto p-6">
        {/* Header */}
        <div className="bg-white rounded-xl shadow-sm border border-gray-100 p-6 mb-6">
          <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-4">
            <div>
              <h1 className="text-3xl font-bold text-gray-900 mb-2">
                Company Management Dashboard
              </h1>
              <p className="text-gray-600">
                Manage recruitment drives, track applications, and monitor
                company relationships
              </p>
            </div>

            <div className="flex items-center gap-3">
              <div className="flex bg-gray-100 rounded-lg p-1">
                <button
                  onClick={() => setViewMode("table")}
                  className={`px-3 py-1 rounded-md text-sm font-medium transition-colors ${
                    viewMode === "table"
                      ? "bg-white text-gray-900 shadow-sm"
                      : "text-gray-600"
                  }`}
                >
                  Table
                </button>
                <button
                  onClick={() => setViewMode("cards")}
                  className={`px-3 py-1 rounded-md text-sm font-medium transition-colors ${
                    viewMode === "cards"
                      ? "bg-white text-gray-900 shadow-sm"
                      : "text-gray-600"
                  }`}
                >
                  Cards
                </button>
              </div>

              <button
                onClick={() => setShowAddModal(true)}
                className="bg-gradient-to-r from-blue-600 to-blue-700 hover:from-blue-700 hover:to-blue-800 text-white px-6 py-2.5 rounded-lg flex items-center gap-2 transition-all duration-200 shadow-sm hover:shadow-md"
              >
                <Plus size={20} />
                Add Company
              </button>
            </div>
          </div>

          {/* Enhanced Stats Cards */}
          <div className="grid grid-cols-2 lg:grid-cols-6 gap-4 mt-6">
            <div className="bg-gradient-to-r from-blue-50 to-indigo-50 p-4 rounded-xl border border-blue-100">
              <div className="flex items-center gap-3">
                <Building2 className="text-blue-600" size={24} />
                <div>
                  <p className="text-blue-600 text-sm font-medium">
                    Total Companies
                  </p>
                  <p className="text-2xl font-bold text-blue-900">
                    {stats.total}
                  </p>
                </div>
              </div>
            </div>

            <div className="bg-gradient-to-r from-yellow-50 to-orange-50 p-4 rounded-xl border border-yellow-100">
              <div className="flex items-center gap-3">
                <Award className="text-yellow-600" size={24} />
                <div>
                  <p className="text-yellow-600 text-sm font-medium">Marquee</p>
                  <p className="text-2xl font-bold text-yellow-900">
                    {stats.marquee}
                  </p>
                </div>
              </div>
            </div>

            <div className="bg-gradient-to-r from-green-50 to-emerald-50 p-4 rounded-xl border border-green-100">
              <div className="flex items-center gap-3">
                <Users className="text-green-600" size={24} />
                <div>
                  <p className="text-green-600 text-sm font-medium">Active</p>
                  <p className="text-2xl font-bold text-green-900">
                    {stats.active}
                  </p>
                </div>
              </div>
            </div>

            <div className="bg-gradient-to-r from-purple-50 to-violet-50 p-4 rounded-xl border border-purple-100">
              <div className="flex items-center gap-3">
                <Clock className="text-purple-600" size={24} />
                <div>
                  <p className="text-purple-600 text-sm font-medium">
                    Upcoming
                  </p>
                  <p className="text-2xl font-bold text-purple-900">
                    {stats.upcoming}
                  </p>
                </div>
              </div>
            </div>

            <div className="bg-gradient-to-r from-gray-50 to-slate-50 p-4 rounded-xl border border-gray-100">
              <div className="flex items-center gap-3">
                <Calendar className="text-gray-600" size={24} />
                <div>
                  <p className="text-gray-600 text-sm font-medium">Completed</p>
                  <p className="text-2xl font-bold text-gray-900">
                    {stats.completed}
                  </p>
                </div>
              </div>
            </div>

            {/* TODO */}
            <div className="bg-gradient-to-r from-gray-50 to-slate-50 p-4 rounded-xl border border-gray-100">
              {" "}
              Export Data
            </div>
          </div>
        </div>

        {/* Filters and Search */}
        <div className="bg-white rounded-xl shadow-sm border border-gray-100 p-6 mb-6">
          {/* Tabs */}
          <div className="flex flex-wrap gap-2 mb-6">
            {[
              { key: "all", label: "All Companies", icon: Building2 },
              { key: "marquee", label: "Marquee", icon: Award },
              { key: "active", label: "Active", icon: Users },
              { key: "upcoming", label: "Upcoming", icon: Clock },
            ].map((tab) => {
              const IconComponent = tab.icon;
              return (
                <button
                  key={tab.key}
                  onClick={() => setActiveTab(tab.key)}
                  className={`px-4 py-2.5 rounded-lg font-medium transition-all duration-200 flex items-center gap-2 ${
                    activeTab === tab.key
                      ? "bg-gradient-to-r from-blue-600 to-blue-700 text-white shadow-md"
                      : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                  }`}
                >
                  <IconComponent size={16} />
                  {tab.label}
                </button>
              );
            })}
          </div>

          {/* Search and Filters */}
          <div className="flex flex-col lg:flex-row gap-4">
            <div className="flex-1 relative">
              <Search
                className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400"
                size={20}
              />
              <input
                type="text"
                placeholder="Search companies, descriptions, or positions..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full pl-10 pr-4 py-3 border border-gray-200 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 bg-gray-50 focus:bg-white transition-all"
              />
            </div>

            <div className="flex gap-3">
              <select
                value={filterStatus}
                onChange={(e) => setFilterStatus(e.target.value)}
                className="px-4 py-3 border border-gray-200 rounded-lg focus:ring-2 focus:ring-blue-500 bg-gray-50 focus:bg-white"
              >
                <option value="all">All Status</option>
                <option value="active">Active</option>
                <option value="upcoming">Upcoming</option>
                <option value="completed">Completed</option>
              </select>

              <select
                value={filterType}
                onChange={(e) => setFilterType(e.target.value)}
                className="px-4 py-3 border border-gray-200 rounded-lg focus:ring-2 focus:ring-blue-500 bg-gray-50 focus:bg-white"
              >
                <option value="all">All Types</option>
                <option value="tech">Technology</option>
                <option value="service">Service</option>
                <option value="consulting">Consulting</option>
                <option value="finance">Finance</option>
              </select>
            </div>
          </div>
        </div>

        {/* Companies Display */}
        {viewMode === "cards" ? (
          <div className="grid grid-cols-1 lg:grid-cols-2 xl:grid-cols-3 gap-6">
            {filteredCompanies.map((company) => (
              <CompanyCardView
                key={company.id}
                company={company}
                statusColors={statusColors}
                companyTypeColors={companyTypeColors}
              />
            ))}
          </div>
        ) : (
          <CompanyTableView
            filteredCompanies={filteredCompanies}
            statusColors={statusColors}
            companyTypeColors={companyTypeColors}
          />
        )}

        {/* Company Detail Modal */}
        {selectedCompany && <CompanyDetailModal />}
      </div>
    </div>
  );
};

const WrappedCompanyListing = () => (
  <CompaniesProvider>
    <CompanyListing />
  </CompaniesProvider>
);

export default WrappedCompanyListing;
